import React, { Component } from 'react';
import { withRouter, Link } from "react-router-dom"
import Modal from './../components/Modal';
import Data from './../data/footer';
import './footer.css';

class Footer extends Component {
	render() {
		const { hash, pathname } = this.props.location;
		return(
			<footer className={`footer ${hash === "#booknow" ? `open` : `closed`}`}>
				<div className="cta-area">
					<Link
						className="cta Amatic"
						to={hash === "#booknow" ? `${pathname}` : `${pathname}#booknow`}
					>
						{ hash === "#booknow" ? "Close" : "Book Now" } »
					</Link>
					<div className="social">
						{Data.map((icon, i) => {
							return (
								<a
									key={i}
									href={icon.url}
									target="_blank"
									name={icon.social}
								>
									<i className={`icon-font icon-${icon.social}`} />
								</a>
							);
						})}
					</div>
				</div>
				<Modal
					active={hash  === "#booknow" ? "active" : "innactive"}
				/>
			</footer>
		);
	}
}

export default withRouter(Footer);


