import React from 'react';
import { Link } from 'react-router-dom';
import './back.css';

const Back = (props) => {
	return(
		<Link 
			className={`back ${props.currentpage}`}
			exact={true}
			to={`${process.env.PUBLIC_URL}/home`}
		>
			<i className="icon-font icon-home" />
		</Link>
	)
}

export default Back;


